import noData from '@/components/noData'
import back from '@/components/back'

export default {
    components:{
        noData,
        back
    },
    data(){
        return {
            DiseaseName: [],
            search: '',
            DiseaseList: [],
            page:1,
            haveMore: true,
            user: this.$store.state.userInfo,
            DiseaseNameSend: []
        }
    },
    watch: {
        search(){
            this.searchFor()
        }
    },
    created(){
        this.searchFor(false)
        this.pull()
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
        searchFor(h){
            this.$post('SecondDiagnosisAPI/MedicalService/QueryDiseaseByPage',{
                DiseaseName: this.search,
                PageIndex: this.page,
                PageSize: 15
            }).then(o => {
                if (!o.ReturnData || o.ReturnData.length < 15){
                    this.haveMore = false
                }
                else {
                    this.haveMore = true
                }
                
                if (h){
                    o.ReturnData.forEach(v => {
                        this.DiseaseList.push(v)
                    })
                }
                else {
                    this.DiseaseList = o.ReturnData
                }
            })
        },
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight

                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.searchFor(true)
                    }
                }, 100)
            }
        },
        pop(idx){
            this.DiseaseName.splice(idx,1)
            this.DiseaseNameSend.splice(idx,1)
        },
        pull(){
            this.$get('SecondDiagnosisAPI/MedicalService/QueryDoctorDiseases',{
                DrId: this.user.Data.User.DoctorId
            }).then(o => {
                o.forEach(v => {
                    this.DiseaseNameSend.push({
                        DiseaseName: v.DiseaseName,
                        DiseaseId: v.DiseaseId
                    })
                    this.DiseaseName.push(v.DiseaseName)
                })
            })
        },
        confirm(){
            this.$bridge.callhandler('DiseaseList', JSON.stringify(this.DiseaseNameSend))
        },
        choose(i){
            const idx = this.DiseaseName.indexOf(i.DiseaseName)
            if(idx == -1){
                this.DiseaseName.push(i.DiseaseName)
                this.DiseaseNameSend.push(i)
            }
            else{
                this.DiseaseName.splice(idx, 1)
                this.DiseaseNameSend.splice(idx, 1)
            }
        }
    }
}



